import { createStore } from "vuex"
import axios from 'axios'

import post from '@/assets/data.js'

const store = createStore({
  state () {
    return {
      post: post,
      follower: [],
    }
  },
  mutations: {
    setAddPost(state, data){
      state.post.unshift(data);
    },
    setMore(state, data){
      state.post.push(data);
    },
    setLike(state, data){
      const idx = data;
      state.post[idx].liked = !state.post[idx].liked;
      state.post[idx].likes = (state.post[idx].liked) ? state.post[idx].likes+1 : state.post[idx].likes-1;
    }
  },
  // ajax ÏöîÏ≤≠
  actions: {
    getData(context){
      axios.get(`https://codingapple1.github.io/vue/more0.json`)
      .then( result => {
        // console.log(result.data);
        context.commit('setMore', result.data);   // axiosÎ°ú Í∞ÄÏ†∏Ïò® Îç∞Ïù¥ÌÑ∞Î•º mutations setMoreÏùÑ Ïù¥Ïö©Ìï¥ Ï†ÄÏû•ÌïúÎã§. (Ï†ÄÏû•ÏùÑ Î∞îÎ°ú Ìï† Ïàò ÏóÜÍ∏∞ ÎïåÎ¨∏Ïóê commitÎ¨∏ÏùÑ ÏîÄ)
      })
      .catch( error =>{
        console.log(error);
      })
    }
  }
})

export default store;